"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Label } from "@/components/ui/label"
import { useRouter } from "next/navigation"

export default function RegisterPage() {
  const router = useRouter()
  const [formData, setFormData] = useState({
    id: "",
    name: "",
    email: "",
    password: "",
    confirmPassword: "",
  })
  const [error, setError] = useState("")
  const [success, setSuccess] = useState("")

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError("")
    setSuccess("")

    if (formData.password !== formData.confirmPassword) {
      setError("Kata laluan tidak sepadan")
      return
    }

    // In a real app, this would be an API call to register the user
    try {
      // Simulating API call
      console.log("Registering user:", formData)
      setSuccess("Pendaftaran berjaya! Penyelia baru telah didaftarkan.")
      // Reset form
      setFormData({
        id: "",
        name: "",
        email: "",
        password: "",
        confirmPassword: "",
      })
    } catch (err) {
      setError("Ralat semasa pendaftaran. Sila cuba lagi.")
    }
  }

  return (
    <main className="min-h-screen bg-gradient-to-br from-gray-900 to-gray-800 flex items-center justify-center p-4">
      <Card className="w-full max-w-md p-6 bg-white/10 backdrop-blur-lg rounded-lg shadow-xl">
        <div className="flex justify-between items-center mb-6">
          <h1 className="text-2xl font-bold text-white">Daftar Penyelia Baru</h1>
          <Button
            variant="outline"
            onClick={() => router.push("/dashboard")}
            className="text-sm"
          >
            Kembali
          </Button>
        </div>

        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <Label className="text-white" htmlFor="id">ID Penyelia</Label>
            <Input
              id="id"
              placeholder="Contoh: P004"
              value={formData.id}
              onChange={(e) => setFormData({ ...formData, id: e.target.value })}
              className="bg-white/20 text-white placeholder:text-gray-400"
              required
            />
          </div>

          <div className="space-y-2">
            <Label className="text-white" htmlFor="name">Nama Penuh</Label>
            <Input
              id="name"
              placeholder="Masukkan nama penuh"
              value={formData.name}
              onChange={(e) => setFormData({ ...formData, name: e.target.value })}
              className="bg-white/20 text-white placeholder:text-gray-400"
              required
            />
          </div>

          <div className="space-y-2">
            <Label className="text-white" htmlFor="email">Emel</Label>
            <Input
              id="email"
              type="email"
              placeholder="contoh@hospital.com"
              value={formData.email}
              onChange={(e) => setFormData({ ...formData, email: e.target.value })}
              className="bg-white/20 text-white placeholder:text-gray-400"
              required
            />
          </div>

          <div className="space-y-2">
            <Label className="text-white" htmlFor="password">Kata Laluan</Label>
            <Input
              id="password"
              type="password"
              placeholder="Masukkan kata laluan"
              value={formData.password}
              onChange={(e) => setFormData({ ...formData, password: e.target.value })}
              className="bg-white/20 text-white placeholder:text-gray-400"
              required
            />
          </div>

          <div className="space-y-2">
            <Label className="text-white" htmlFor="confirmPassword">Sahkan Kata Laluan</Label>
            <Input
              id="confirmPassword"
              type="password"
              placeholder="Masukkan semula kata laluan"
              value={formData.confirmPassword}
              onChange={(e) => setFormData({ ...formData, confirmPassword: e.target.value })}
              className="bg-white/20 text-white placeholder:text-gray-400"
              required
            />
          </div>

          {error && (
            <p className="text-red-400 text-sm text-center">{error}</p>
          )}
          {success && (
            <p className="text-green-400 text-sm text-center">{success}</p>
          )}

          <Button type="submit" className="w-full">
            Daftar Penyelia
          </Button>
        </form>
      </Card>
    </main>
  )
}
